  $click: 0px 179px 0px -179px 59px 59px 305px 238px 'sprite.png';
  $comp: 63px 179px -63px -179px 59px 59px 305px 238px 'sprite.png';
  $logo: 0px 130px 0px -130px 216px 45px 305px 238px 'sprite.png';
  $mail: 182px 179px -182px -179px 46px 45px 305px 238px 'sprite.png';
  $man: 0px 0px 0px 0px 126px 126px 305px 238px 'sprite.png';
  $mouse: 260px 50px -260px -50px 35px 56px 305px 238px 'sprite.png';
  $phone: 260px 0px -260px 0px 45px 46px 305px 238px 'sprite.png';
  $place: 260px 110px -260px -110px 30px 46px 305px 238px 'sprite.png';
  $users: 126px 179px -126px -179px 52px 52px 305px 238px 'sprite.png';
  $woman: 130px 0px -130px 0px 126px 126px 305px 238px 'sprite.png';

  @mixin sprite-position($sprite) {
    $sprite-offset-x: nth($sprite, 3);
    $sprite-offset-y: nth($sprite, 4);
    background-position: $sprite-offset-x  $sprite-offset-y;
  }

  @mixin sprite-image($sprite) {
    $sprite-image: nth($sprite, 9);
    background-image: url(%=static=%png-sprite/96dpi/#{$sprite-image});
  }

  @mixin sprite-mixin-template($sprite) {
    @include sprite-image($sprite);
    @include sprite-position($sprite);
    width: nth($sprite, 5);
    height: nth($sprite, 6);
  }

  @mixin bg($sprite, $repeat: no-repeat) {
    @include sprite-mixin-template($sprite);
    $sprite-total-width: nth($sprite, 7);
    $sprite-total-height: nth($sprite, 8);
    background-size: $sprite-total-width, $sprite-total-height;
    background-repeat: $repeat;



  }
